import { useEffect, useState } from "react";
import InfiniteScroll from "react-infinite-scroll-component";
import { getAllProducts } from "../../api/categories";
import { BreadCrumbs } from "./components/BreadCrumbs/BreadCrumbs";
import { ProductsNotFound } from "../productsNotFound/ProductsNotFound";
import { Loader } from "../../components/loader/Loader";
import Card from "../../components/card/Card";

export const AllDiscount = () => {
  const [discountProducts, setDiscountProducts] = useState([]);
  const [loading, setLoading] = useState(true);
  const [hasMore, setHasMore] = useState(true);
  const [offset, setOffset] = useState(0);

  const loadMore = () => {
    getAllProducts({ type: "discount", offset: offset })
      .then(({ products }) => {
        if (products.length === 0) {
          setHasMore(false);
        } else {
          setDiscountProducts((prevProducts) => [...prevProducts, ...products]);
          setOffset(offset + 12);
        }
        setLoading(false);
      })
      .catch((error) => {
        console.error("    : ", error);
      });
  };

  useEffect(() => {
    loadMore();
  }, []);

  return (
    <>
      <BreadCrumbs />
      <div className="products">
        <div className="sale__container">
          <h2 className="category2__title title"> </h2>

          {!loading && discountProducts.length === 0 && <ProductsNotFound />}
          {loading && <Loader />}

          <InfiniteScroll
            dataLength={discountProducts.length}
            next={loadMore}
            hasMore={hasMore}
            loader={!loading && <Loader />}
            style={{ overflow: 'unset' }}
          >
            <div className="sale__grid-layout">
              {discountProducts.map((product, index) => (
                <Card key={index} data={product} />
              ))}
            </div>
          </InfiniteScroll>
        </div>
      </div>
    </>
  );
};
